*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: Helvetica;
}

body{
    background-color: #393646
}

#root{
    display: flex;
    flex-direction: row;
    height: 100vh;
    width: 100%;
}

.sideBar{
    flex-basis: 20%;
    height: 100%;
    background-color: #202123;
    padding-top: 20px;
}

.navBar{
    position:sticky;
    top: 0;
    right: 0;
    background-color: #6D5D6E;
    color: #ffffff;
    width: 100%;
    height: 50px;
}

select{
    color: #ffffff;
}

.main{
    overflow:auto;
    flex-basis: 80%;

}

button{
    margin: 5px;
    color: #F4EEE0;
}

button:hover{
    background-color: #2e2f3a;
}

input:hover{
    background-color: #2a2b32;
}

.navBar{
    display: flex;
    justify-content: right;
}

.navBar img{
    width: 50px;
    height: 50px;
    margin: 0 20px;
}

.navBar p{
    opacity: 0.5;
    margin: auto 20px;
}

.registerBtn{
    color: #5094ed;
    margin: 0 20px;
    cursor: pointer;
}

.registerBtn:hover{
    font-weight: bold;
}

.registerErrors{
    color: red;
    font-size: 0.8rem;
    overflow: hidden;
}

.registerStatus{
    display: flex; 
}
